import {
    Fade,
    LinearProgress,
    Paper,
    Slide,
    SwipeableDrawer,
} from "@mui/material";
import "./css/glass-button.css";
import React, { useEffect, useMemo, useState } from "react";
import { grey, lightBlue, orange, red, yellow } from "@mui/material/colors";
import Grid from "@mui/material/Unstable_Grid2/Grid2";
import { TypeAnimation } from "react-type-animation";
import UpgradeCard from "./upgradeCard";
import DrawerContent from "./drawerContent";
import useSound from "use-sound";
import Particles from "@tsparticles/react";
import BoostDrawer from "./boostDrawer";
import { useNavigate } from "react-router-dom";

const Boosts = ({
    transition,
    theme,
    setTheme,
    coinsPerTap,
    maxEnergy,
    chargePerSecond,
    colors,
    prices,
    axios,
    setChargePerSecond,
    setCoinsPerTap,
    setSnackBar,
    balance,
    setBalance,
    P,
    setMaxEnergy,
    setEnergy,
    settings,
    remainingMaxEnergy,
    setRemainingMaxEnergy,
    maximumMaxEnergy,
    // remainingTurbo,
    // maximumTurbo,
}) => {
    const [textColor, setTextColor] = useState(orange[700]);
    const [upgradeDrawer, setUpgradeDrawer] = useState([false, <></>]);
    const [loading, setLoading] = useState(false);
    const logo = "/assets/images/logo1.png";
    const click_sound = "/assets/sounds/click1.mp3";
    const [playClickSound] = useSound(click_sound);
    const Particle = useMemo(() => <Particles options={P} />, []);
    const telegram = window.Telegram.WebApp;
    const navigate = useNavigate();
    telegram.BackButton.show();
    telegram.BackButton.onClick(() => {
        navigate("/click-game");
        telegram.BackButton.hide();
    });

    return (
        <div>
            {Particle}
            <Grid
                sx={{
                    height: "100vh",
                    bottom: 0,
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    padding: "1.2rem",
                    background: "none",
                    backdropFilter: "blur(0px)",
                    WebkitBackdropFilter: "blur(0px)",
                    padding: 2,
                    border: "1px solid " + colors[theme],
                    boxShadow: "inset 0 5px 20px " + colors[theme],
                    zIndex: 1,
                }}
                container="true"
            >
                <Grid
                    xs={12}
                    width="100%"
                    height="14vh"
                    position={"fixed"}
                    top={0}
                >
                    <Slide in={transition}>
                        <Paper
                            elevation={12}
                            sx={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                background: grey[900],
                                borderRadius: "0",
                                width: "100%",
                                borderBottom: "1px solid " + theme,
                            }}
                        >
                            <h1
                                style={{
                                    width: "100%",
                                    fontSize: 30,
                                    textAlign: "center",
                                }}
                            >
                                <Grid xs={12}>
                                    <span>
                                        <TypeAnimation
                                            sequence={["Get Over Here..."]}
                                            repeat={0}
                                            cursor={false}
                                            speed={100}
                                        />
                                    </span>{" "}
                                </Grid>
                                <Grid xs={12}>
                                    <span style={{ color: textColor }}>
                                        <TypeAnimation
                                            sequence={[
                                                "Power",
                                                1500,
                                                () => {
                                                    setTimeout(() => {
                                                        setTextColor(red[600]);
                                                    }, 450);
                                                },
                                                "Strength",
                                                1500,
                                                () => {
                                                    setTimeout(() => {
                                                        setTextColor(
                                                            lightBlue[500]
                                                        );
                                                    }, 725);
                                                },
                                                "Bi-Han",
                                                1500,
                                                () => {
                                                    setTimeout(() => {
                                                        setTextColor(
                                                            yellow[800]
                                                        );
                                                    }, 600);
                                                },
                                                "Kuai Liang",
                                                1500,
                                                () => {
                                                    setTimeout(() => {
                                                        setTextColor(
                                                            orange[700]
                                                        );
                                                    }, 900);
                                                },
                                            ]}
                                            repeat={Infinity}
                                            speed={10}
                                            deletionSpeed={10}
                                        />
                                    </span>
                                    !
                                </Grid>
                            </h1>
                        </Paper>
                    </Slide>
                </Grid>
                <Grid
                    xs={12}
                    padding={2}
                    paddingTop={3.5}
                    paddingBottom={3.5}
                    maxWidth={"744px"}
                    marginTop={"6.5rem"}
                    height={"75vh"}
                    marginBottom={"2rem"}
                >
                    <Fade in={transition}>
                        <Paper>
                            <Grid
                                container="true"
                                gap={1}
                                display={"flex"}
                                justifyContent={"center"}
                            >
                                <Grid
                                    xs={5.85}
                                    display={"flex"}
                                    alignItems={"center"}
                                >
                                    <Slide in={transition} direction="right">
                                        <Grid
                                            className={
                                                remainingMaxEnergy == 0 ||
                                                loading
                                                    ? "btn grey"
                                                    : "btn " + theme
                                            }
                                            padding={0.5}
                                            container="true"
                                            width={"100%"}
                                            onClick={() => {
                                                if (
                                                    remainingMaxEnergy == 0 ||
                                                    loading
                                                )
                                                    return;
                                                setUpgradeDrawer([
                                                    true,
                                                    <BoostDrawer
                                                        axios={axios}
                                                        remaining={
                                                            remainingMaxEnergy
                                                        }
                                                        max={maximumMaxEnergy}
                                                        setSnackBar={
                                                            setSnackBar
                                                        }
                                                        setUpgradeDrawer={
                                                            setUpgradeDrawer
                                                        }
                                                        setIsLoading={
                                                            setLoading
                                                        }
                                                        isLoading={loading}
                                                        item="maxEnergy"
                                                        theme={theme}
                                                        setRemaining={
                                                            setRemainingMaxEnergy
                                                        }
                                                        setEnergy={setEnergy}
                                                        maxEnergy={maxEnergy}
                                                    >
                                                        Draw upon the spirits of
                                                        fallen warriors, stoking
                                                        your inner inferno.
                                                        Activate "Soulfire
                                                        Surge" for
                                                        lightning-fast strikes,
                                                        unyielding defenses, and
                                                        bone-crushing
                                                        Fatalities. The
                                                        battlefield trembles as
                                                        your power surges
                                                        embrace it, but tread
                                                        carefully, lest you
                                                        become a wraith
                                                        yourself.
                                                    </BoostDrawer>,
                                                ]);
                                            }}
                                        >
                                            <Grid xs={5}>
                                                {" "}
                                                <img
                                                    src="/assets/images/icons/_8818beac-a147-45a7-b0aa-9b369c95a14b.jpg"
                                                    height={"70rem"}
                                                    style={{
                                                        mixBlendMode:
                                                            "difference",
                                                        filter: "contrast(120%)",
                                                        background:
                                                            "tranparent",
                                                    }}
                                                    alt=""
                                                />
                                            </Grid>
                                            <Grid xs={7}>
                                                <div
                                                    style={{
                                                        marginTop: ".6rem",
                                                        letterSpacing: "1px",
                                                        fontSize: 14,
                                                    }}
                                                >
                                                    Max Energy
                                                </div>
                                                <div
                                                    style={{
                                                        fontSize: "14px",
                                                        color: grey[300],
                                                        letterSpacing: "2px",
                                                    }}
                                                >
                                                    {remainingMaxEnergy} /{" "}
                                                    {maximumMaxEnergy}
                                                    {/* Soon... */}
                                                </div>
                                                <div>
                                                    <LinearProgress
                                                        variant="determinate"
                                                        value={
                                                            (remainingMaxEnergy /
                                                                maximumMaxEnergy) *
                                                            100
                                                        }
                                                        color="warning"
                                                        style={{ width: "95%" }}
                                                    />
                                                </div>
                                            </Grid>
                                        </Grid>
                                    </Slide>
                                </Grid>
                                <Grid
                                    xs={5.85}
                                    display={"flex"}
                                    alignItems={"center"}
                                >
                                    <Slide in={transition} direction="left">
                                        <Grid
                                            className={"btn grey"}
                                            padding={0.5}
                                            container="true"
                                            width={"100%"}
                                        >
                                            <Grid xs={5}>
                                                {" "}
                                                <img
                                                    src="/assets/images/icons/_4adf9429-0813-42b2-a983-1e09d0d2d6a9.jpg"
                                                    height={"70rem"}
                                                    style={{
                                                        transform: "scaleX(-1)",
                                                        mixBlendMode:
                                                            "difference",
                                                        filter: "contrast(120%)",
                                                        background:
                                                            "tranparent",
                                                    }}
                                                    alt=""
                                                />
                                            </Grid>
                                            <Grid xs={7}>
                                                <div
                                                    style={{
                                                        marginTop: ".6rem",
                                                        letterSpacing: "1px",
                                                    }}
                                                >
                                                    Turbo
                                                </div>
                                                <div
                                                    style={{
                                                        fontSize: "14px",
                                                        color: grey[400],
                                                        letterSpacing: "2px",
                                                    }}
                                                >
                                                    {/* {remainingTurbo} /{" "}
                                                    {maximumTurbo} */}
                                                    Soon...
                                                </div>
                                                {/* <div>
                                                    <LinearProgress
                                                        variant="determinate"
                                                        value={0}
                                                        color="error"
                                                    />
                                                </div> */}
                                            </Grid>
                                        </Grid>
                                    </Slide>
                                </Grid>
                            </Grid>
                            <br />
                            <Slide in={transition} direction="left">
                                <div>
                                    <UpgradeCard
                                        Icon={
                                            "assets/images/icons/_7ae4a4ba-dc95-4342-96f7-416ccb4ab7c4.jpg"
                                        }
                                        title={"Koins per tap"}
                                        description={`+${coinsPerTap} | level ${coinsPerTap}`}
                                        theme={theme}
                                        onClick={() => {
                                            if (settings.menuSounds)
                                                playClickSound();
                                            setUpgradeDrawer([
                                                true,
                                                <DrawerContent
                                                    sounds={
                                                        !settings.menuSounds
                                                    }
                                                    balance={balance}
                                                    setUpgradeDrawer={
                                                        setUpgradeDrawer
                                                    }
                                                    setBalance={setBalance}
                                                    axios={axios}
                                                    title={"Koins per tap"}
                                                    maxEnergy={setMaxEnergy}
                                                    setTheme={setTheme}
                                                    chargePerSecond={
                                                        setChargePerSecond
                                                    }
                                                    coinsPerTap={setCoinsPerTap}
                                                    upgrade={[
                                                        "tap",
                                                        coinsPerTap,
                                                    ]}
                                                    price={prices.coinsPerTap}
                                                    theme={theme}
                                                    setSnackBar={setSnackBar}
                                                    logo={logo}
                                                >
                                                    In the depths unseen, a
                                                    slumbering wellspring hums
                                                    with the song of wealth.
                                                    Each click, a resonating
                                                    chord that stirs its
                                                    surface. The more persistent
                                                    your touch, the stronger the
                                                    melody, beckoning forth a
                                                    greater bounty. Invest in
                                                    the unseen symphony and
                                                    unlock the secrets of untold
                                                    riches.
                                                </DrawerContent>,
                                            ]);
                                        }}
                                    />
                                </div>
                            </Slide>
                            <br />
                            <Slide in={transition} direction="right">
                                <div>
                                    <UpgradeCard
                                        Icon={
                                            "assets/images/icons/_97bc1deb-5af8-42dc-86e1-570e73cde617.jpg"
                                        }
                                        title={"Energy tank"}
                                        description={`${
                                            maxEnergy > 1024
                                                ? format(maxEnergy)
                                                : maxEnergy
                                        } | level ${
                                            maxEnergy / prices.maxEnergy
                                        }`}
                                        theme={theme}
                                        onClick={() => {
                                            if (settings.menuSounds)
                                                playClickSound();
                                            setUpgradeDrawer([
                                                true,
                                                <DrawerContent
                                                    sounds={
                                                        !settings.menuSounds
                                                    }
                                                    axios={axios}
                                                    balance={balance}
                                                    setUpgradeDrawer={
                                                        setUpgradeDrawer
                                                    }
                                                    setBalance={setBalance}
                                                    title={"Energy limit"}
                                                    theme={theme}
                                                    setTheme={setTheme}
                                                    setSnackBar={setSnackBar}
                                                    logo={logo}
                                                    maxEnergy={setMaxEnergy}
                                                    chargePerSecond={
                                                        setChargePerSecond
                                                    }
                                                    coinsPerTap={setCoinsPerTap}
                                                    upgrade={[
                                                        "energy",
                                                        maxEnergy,
                                                    ]}
                                                    price={prices.maxEnergy}
                                                >
                                                    A shimmering tapestry
                                                    stretches before you, woven
                                                    from the threads of your
                                                    will. Each click a strand,
                                                    your energy the lifeblood
                                                    that courses through it.
                                                    Upgrades reinforce the
                                                    tapestry's weave, allowing
                                                    it to hold a greater bounty
                                                    of will, extending your
                                                    tireless tapping. Will you
                                                    become the master weaver,
                                                    your will a boundless
                                                    wellspring of clicks?
                                                </DrawerContent>,
                                            ]);
                                        }}
                                    />
                                </div>
                            </Slide>
                            <br />
                            <Slide in={transition} direction="left">
                                <div>
                                    <UpgradeCard
                                        Icon={
                                            "assets/images/icons/_7f90b98e-1053-419c-982e-fc62ae774519.jpg"
                                        }
                                        title={"Charge speed"}
                                        description={`+${chargePerSecond} | level ${chargePerSecond}`}
                                        theme={theme}
                                        onClick={() => {
                                            if (settings.menuSounds)
                                                playClickSound();
                                            setUpgradeDrawer([
                                                true,
                                                <DrawerContent
                                                    sounds={
                                                        !!settings.menuSounds
                                                    }
                                                    axios={axios}
                                                    setUpgradeDrawer={
                                                        setUpgradeDrawer
                                                    }
                                                    balance={balance}
                                                    setBalance={setBalance}
                                                    title={"Recharge speed"}
                                                    setTheme={setTheme}
                                                    maxEnergy={setMaxEnergy}
                                                    chargePerSecond={
                                                        setChargePerSecond
                                                    }
                                                    coinsPerTap={setCoinsPerTap}
                                                    setSnackBar={setSnackBar}
                                                    upgrade={[
                                                        "charge",
                                                        chargePerSecond,
                                                    ]}
                                                    price={
                                                        prices.chargePerSecond
                                                    }
                                                    theme={theme}
                                                    logo={logo}
                                                >
                                                    In the whispering dunes of
                                                    forgotten time, a slumbering
                                                    hourglass awaits. Its sands,
                                                    the essence of fleeting
                                                    moments, hold the key to a
                                                    swifter flow of fortune.
                                                    Legends whisper of those who
                                                    have tamed the sands, their
                                                    whispers coaxing the grains
                                                    to cascade with
                                                    ever-increasing haste. Each
                                                    upgrade a sigil etched upon
                                                    the wind, a plea to the
                                                    hourglass's spirit. Will you
                                                    be the one to unravel the
                                                    mysteries of time's flow and
                                                    unlock a torrent of wealth?
                                                    Invest in the secrets of the
                                                    hourglass, and bend the very
                                                    fabric of time to your will.
                                                </DrawerContent>,
                                            ]);
                                        }}
                                    />
                                </div>
                            </Slide>
                        </Paper>
                    </Fade>
                </Grid>
                <Grid>
                    <SwipeableDrawer
                        keepMounted
                        PaperProps={{
                            sx: {
                                background: grey[900],
                                borderRadius: "12px 12px 0 0",
                                boxShadow: `0 -3px 18px rgb(${colors[theme]})`,
                                borderTop: `1px solid rgb(${colors[theme]})`,
                                textAlign: "center",
                                padding: "5px",
                                paddingBottom: "0px",
                            },
                        }}
                        slotProps={{
                            backdrop: {
                                sx: { bgcolor: "rgba(15, 15, 15, .90)" },
                            },
                        }}
                        anchor="bottom"
                        sx={{ zIndex: 999999 }}
                        allowSwipeInChildren={true}
                        disableSwipeToOpen={true}
                        open={upgradeDrawer[0]}
                        onOpen={() => {}}
                        title="Upgrade"
                        onClose={() => {
                            setUpgradeDrawer([false, <></>]);
                        }}
                    >
                        {upgradeDrawer[1]}
                    </SwipeableDrawer>
                </Grid>
            </Grid>
        </div>
    );

    function format(number) {
        const formatter = new Intl.NumberFormat("en", {
            notation: "compact",
            compactDisplay: "short",
            maximumSignificantDigits: 3,
        });

        return formatter.format(number);
    }
};

export default Boosts;
