import { Box, CircularProgress } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const BoostDrawer = ({
    setIsLoading,
    isLoading,
    item,
    theme,
    children,
    remaining,
    axios,
    setUpgradeDrawer,
    setSnackBar,
    max,
    setRemaining,
    setEnergy,
    maxEnergy,
}) => {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(isLoading);

    useEffect(() => {
        console.log(loading);
    }, [loading]);

    return (
        <Grid container="true" padding={"1rem"}>
            <Grid xs={12} letterSpacing={"1px"} textAlign={"start"}>
                {children}
            </Grid>
            <Grid
                xs={12}
                display={"flex"}
                alignItems={"end"}
                justifyContent={"center"}
            >
                <button
                    style={{
                        padding: "10px",
                        color: "white",
                        fontFamily: "mortal-kombat",
                        fontSize: "32px",
                        marginTop: "1.5rem",
                    }}
                    className={loading ? "btn white blink" : `btn ${theme}`}
                    disabled={loading}
                    onClick={() => {
                        if (remaining == 0 || loading) return;
                        setLoading(true);
                        axios
                            .post("/boost/submit", {
                                item,
                            })
                            .then(({ data }) => {
                                setRemaining(data.remaining);
                                setSnackBar([
                                    "Boost activated.",
                                    "success",
                                    true,
                                ]);
                                setUpgradeDrawer([false, <></>]);
                                setLoading(false);
                                setEnergy(maxEnergy);
                                if (item == "turbo") navigate("/click-game");
                            })
                            .catch((error) => {
                                console.log(error);
                                setLoading(false);
                                setSnackBar([
                                    error.response?.data?.message ||
                                        "Error fetching data.",
                                    "error",
                                    true,
                                ]);
                            });
                    }}
                >
                    {loading ? <CircularProgress /> : ""}
                    {loading ? "Fetching data..." : "Activate"}
                </button>
            </Grid>
        </Grid>
    );
};

export default BoostDrawer;
