import { CircularProgress } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import React, { useState } from "react";
import useSound from "use-sound";

const DrawerContent = ({
    price,
    theme,
    children,
    logo,
    title,
    upgrade,
    axios,
    setSnackBar,
    setBalance,
    balance,
    setUpgradeDrawer,
    chargePerSecond,
    coinsPerTap,
    maxEnergy,
}) => {
    const [loading, setLoading] = useState(false);
    const upgrades = {
        charge: "chargePerSecond",
        tap: "coinsPerTap",
        energy: "maxEnergy",
    };
    const click_sound = "/assets/sounds/click2.mp3";
    const [playClickSound] = useSound(click_sound);
    const atMaxLevel =
        upgrades[upgrade[0]] == "chargePerSecond" && upgrade[1] / price >= 5;

    const details = {
        price:
            Math.floor(
                upgrade[0] == "charge"
                    ? 2 ** (upgrade[1] + price)
                    : 2 ** ((upgrade[1] + price) / price)
            ) * 1024,
        whatToUpgrade: upgrades[upgrade[0]],
    };
    return (
        <Grid
            container="true"
            display={"flex"}
            direction={"row"}
            justifyContent={"center"}
        >
            <Grid>
                <h1
                    style={{
                        top: 0,
                    }}
                >
                    {title}
                </h1>
                <p
                    style={{
                        textAlign: "start",
                        padding: "6px",
                    }}
                >
                    {children}
                </p>
            </Grid>
            <Grid
                sx={{
                    left: 0,
                    right: 0,
                    marginBottom: ".55rem",
                }}
            >
                <button
                    className={!loading ? `btn ${theme}` : "btn white blink"}
                    style={{
                        padding: "10px",
                        color: "#fff",
                    }}
                    disabled={loading || atMaxLevel}
                    onClick={onClick}
                >
                    <Grid
                        style={{
                            fontSize: "30px",
                            fontFamily: "mortal-kombat",
                        }}
                        container="true"
                        display={"flex"}
                        alignItems={"center"}
                    >
                        <Grid>
                            {!loading ? (
                                <img
                                    src={logo}
                                    alt=""
                                    height={"52px"}
                                    width={"52px"}
                                />
                            ) : (
                                <CircularProgress />
                            )}
                        </Grid>
                        <Grid marginLeft={".2rem"}>
                            {!loading ? (
                                atMaxLevel ? (
                                    <span>Max level</span>
                                ) : (
                                    <span>
                                        {format(
                                            parseInt(details.price)
                                        ).toLocaleString()}{" "}
                                        Koins
                                    </span>
                                )
                            ) : (
                                <span>Fetching data</span>
                            )}
                        </Grid>
                    </Grid>
                </button>
            </Grid>
        </Grid>
    );

    function onClick() {
        if (balance - details.price < 0)
            return setSnackBar(["Insufficient balance.", "error", true]);

        setLoading(true);
        axios
            .post("/upgrade/upgrade", {
                item: details.whatToUpgrade,
            })
            .then(({ data }) => {
                setBalance(balance - parseInt(details.price));
                setSnackBar(["Upgraded successfully.", "success", true, false]);
                setUpgradeDrawer([false, <></>]);
                eval(`${details.whatToUpgrade}(${data.result})`);
                playClickSound();
            })
            .catch((error) => {
                console.log(error);
                return setSnackBar([
                    error.response?.data?.message || "Error fetching data.",
                    "error",
                    true,
                ]);
            })
            .finally(() => setLoading(false));
    }

    function format(number) {
        const formatter = new Intl.NumberFormat("en", {
            notation: "compact",
            compactDisplay: "short",
            maximumSignificantDigits: 3,
        });

        return formatter.format(number);
    }
};

export default DrawerContent;
