import { Divider } from "@mui/material";
import { grey } from "@mui/material/colors";
import Grid from "@mui/material/Unstable_Grid2/Grid2";

const UpgradeCard = ({ theme, title, description, Icon, onClick }) => {
    return (
        <div
            role="button"
            onClick={onClick}
            style={{
                padding: "7.5px",
                backgroundColor: "rgba(25,25,25,1)",
                boxShadow: `0 3px 10px ${theme}`,
            }}
            className={"btn " + theme}
        >
            <Grid container="true">
                <Grid xs={9}>
                    <span style={{ fontSize: 22 }}>{title}</span>
                    <br />
                    <span style={{ fontSize: 12 }}>{description}</span>
                    <br />
                </Grid>
                <Grid
                    xs={3}
                    display={"flex"}
                    flexDirection={"row"}
                    fontSize={20}
                >
                    <Divider
                        orientation="vertical"
                        flexItem
                        sx={{
                            left: 0,
                            borderColor: grey[300],
                            borderRightWidth: 2,
                        }}
                    />
                    <span
                        style={{
                            alignSelf: "start",
                            justifySelf: "end",
                            textAlign: "center",
                            right: 15,
                            fontSize: "50px",
                            position: "absolute",
                        }}
                    >
                        <img
                            src={Icon}
                            alt=""
                            height={"54px"}
                            style={{
                                mixBlendMode: "difference",
                                filter: "contrast(140%)",
                                background: "tranparent",
                            }}
                        />
                    </span>
                </Grid>
            </Grid>
        </div>
    );
};

export default UpgradeCard;
